
































import server from '../../store/server.js';

export default {
	data() {
		return {
			title: 'media-list',
			showImg: false,
			img: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg?imageView2/3/w/200/h/100/q/90",
				userid:'',
				username:''
		}
	},
	methods:{
		jumpUser(){
			
		},
		jumpAbout(){
			uni.navigateTo({
				url: 'aboutus'
			})
		},
		jumpInstru(){
			uni.navigateTo({
				url: 'instruction'
			})
		},
		exit(){
			server.net.ClearSessionToken();
			server.net.ClearLocalToken();
			uni.reLaunch({ 
				 url: '../login/login'
			})
		}
	},
	onLoad() {
		let user = server.net.GetUser();
		this.userid = user.AccountID;
		this.username = user.Name;
		setTimeout(() => {
			this.showImg = true;
		}, 400)
	}
}



